<script setup>
import { ref, defineProps } from "vue";
import { useStore } from '@/stores/store'
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogPanel,
  DialogTitle,
} from "@headlessui/vue";

const store = useStore()

defineProps({
  title: {
    type: String,
    required: true
  },
  description: {
    type: String,
    required: false
  },
  buttonText: {
    type: String,
    required: true
  }
})

const primaryButtonRef = ref(null)

function closeModal() {
  store.setShowModal(false)
}
</script>

<template>
  <TransitionRoot appear :show="store.showModal" as="template">
    <Dialog as="div" :initialFocus="primaryButtonRef" @close="closeModal" class="relative z-100">
      <TransitionChild as="template" enter="duration-300 ease-out" enter-from="opacity-0" enter-to="opacity-100"
        leave="duration-200 ease-in" leave-from="opacity-100" leave-to="opacity-0">
        <div class="fixed inset-0 bg-black bg-opacity-25 dark:bg-base-500 dark:bg-opacity-25" />
      </TransitionChild>

      <div class="fixed inset-0 overflow-y-auto">
        <div class="flex min-h-full items-center justify-center p-4 text-center">
          <TransitionChild as="template" enter="duration-300 ease-out" enter-from="opacity-0 scale-95"
            enter-to="opacity-100 scale-100" leave="duration-200 ease-in" leave-from="opacity-100 scale-100"
            leave-to="opacity-0 scale-95">
            <DialogPanel
              class="w-full max-w-md transform overflow-hidden rounded-xl bg-white p-6 text-left align-middle shadow-xl transition-all dark:bg-base-800">
              <button class="btn btn-default absolute top-2 right-2 border-none md:top-4 md:right-4"
                @click="closeModal">
                <font-awesome-icon icon="fas fa-xmark" />
              </button>
              <DialogTitle as="h5" class="mb-4 capitalize">
                {{ title }}
              </DialogTitle>
              <div class="my-4">
                <p>
                  {{ description }}
                </p>
              </div>

              <div class="mt-4 flex flex-row-reverse">
                <button ref="primaryButtonRef" type="button" class="btn btn-primary" @click="closeModal">
                  {{ buttonText }}
                </button>
              </div>
            </DialogPanel>
          </TransitionChild>
        </div>
      </div>
    </Dialog>
  </TransitionRoot>
</template>
