<script setup>
import { onMounted } from "vue"
import { RouterLink } from 'vue-router'
import { useStore } from '@/stores/store'

const store = useStore()

const toggleWhiteLabelTheme = () => {
  store.whiteLabelTheme = !store.whiteLabelTheme
  if (store.whiteLabelTheme) {
    localStorage.setItem('theme', 'custom-theme');
  } else {
    localStorage.removeItem('theme')
  }
  updateTheme()
}

const toggleMode = (mode) => {
  store.mode = mode;
  if (mode) {
    localStorage.setItem('mode', mode);
  } else {
    localStorage.removeItem('mode')
  }
  updateMode()
}

const updateTheme = () => {
  const theme = localStorage.getItem('theme');

  if (theme === 'custom-theme') {
    // Add the 'custom-theme' class to the body
    document.querySelector('body').classList.add('custom-theme');
  } else {
    // Remove the 'custom-theme' class from the body
    document.querySelector('body').classList.remove('custom-theme');
  }
}

const updateMode = () => {
  if (localStorage.getItem('mode') === 'dark' || (!('mode' in localStorage)) && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    document.querySelector('html').classList.add('dark')
    store.isSystemDarkMode = true
  } else {
    document.querySelector('html').classList.remove('dark')
    store.isSystemDarkMode = false
  }
}

onMounted(() => {
  updateTheme()
  updateMode()
  const darkModePreference = window.matchMedia("(prefers-color-scheme: dark)");
  darkModePreference.addEventListener("change", e => e.matches ? updateMode('dark') : updateMode('light'));
})
</script>

