<script setup>
import { ref, defineProps } from 'vue'
import { Switch, SwitchLabel, SwitchGroup } from '@headlessui/vue'

const props = defineProps({
  label: {
    type: String,
    default: ''
  },
  enabled: {
    type: Boolean,
    default: false
  },
  disabled: {
    type: Boolean,
    default: false
  }
})

const enabled = ref(props.enabled)
</script>

<template>
  <SwitchGroup>
    <div class="flex items-center">
      <Switch v-model="enabled" @click="$emit('onClick')"
        :class="enabled ? 'bg-primary-500' : 'bg-primary-100 dark:bg-base-600'" :disabled="disabled"
        class="relative inline-flex h-5 w-[36px] shrink-0 cursor-pointer disabled:cursor-not-allowed rounded-full border-2 border-transparent transition-colors duration-200 ease-in-out focus:ring focus:ring-offset-2 focus:ring-primary-200 focus:outline-none focus-visible:ring focus-visible:ring-primary-100 dark:ring-offset-neutral-800 dark:focus:ring-primary-800 dark:focus:ring-opacity-60">
        <span class="sr-only">Use setting</span>
        <span aria-hidden="true" :class="enabled ? 'translate-x-4' : 'translate-x-0'"
          class="pointer-events-none inline-block h-4 w-4 transform rounded-full bg-white shadow-lg ring-0 transition duration-200 ease-in-out" />
      </Switch>
      <SwitchLabel class="ml-2 select-none text-base-800 dark:text-base-200">{{ label }}</SwitchLabel>
    </div>
  </SwitchGroup>
</template>
