const value = ref(50)
const min = ref(0)
const max = ref(100)

const sliderBackgroundStyle = computed(() => {
  const rangeValue =
    ((value.value - min.value) /
      (max.value - min.value)) *
    100
  return (
    'linear-gradient(to right, var(--primary-500) 0%, var(--primary-500) ' +
    rangeValue +
    '%, transparent ' +
    rangeValue +
    '%, transparent 100%)'
  )
})
