<script setup>
import { reactive, computed } from 'vue'
import SectionAside from '@/layout/SectionAside.vue'
import { useVuelidate } from '@vuelidate/core'
import {
  required,
  minLength,
  email,
} from '@vuelidate/validators'

// Form Validation Patterns
const onSubmitState = reactive({
  username: '',
  emailAddress: '',
  password: '',
})

const onBlurState = reactive({
  username: '',
  emailAddress: '',
  password: '',
})

const onBlurAndSubmitState = reactive({
  username: '',
  emailAddress: '',
  password: '',
})

const rules = {
  username: {
    required,
  },
  email: {
    required, email
  },
  password: { required, minLength: minLength(8) }
}

const onSubmitRules = computed(() => rules)
const onBlurRules = computed(() => rules)
const onBlurAndSubmitRules = computed(() => rules)

const onSubmit = async (form) => {
  const result = await form.$validate()
  if (result) {
    alert('Success! Form submitted.')
  }
}

const validateOnSubmit = useVuelidate(onSubmitRules, onSubmitState)
const validateOnBlur = useVuelidate(onBlurRules, onBlurState)
const validateOnBlurAndSubmit = useVuelidate(onBlurAndSubmitRules, onBlurAndSubmitState)
</script>

<template>
  <section>
    <p>Form validation should follow a consistent validation pattern.</p>
    <div v-scroll-spy>
      <div id="on-submit" class=" mt-6 mb-12">
        <h5>On Submit</h5>
        <p>The form is validated on submit.</p>
        <form novalidate id="on-submit" @submit.prevent="onSubmit(validateOnSubmit)">
          <div class="my-6">
            <label for="on-submit-username"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">Username</label>
            <input type="text" id="on-submit-username" v-model="onSubmitState.username"
              :class="{ invalid: validateOnSubmit.username.$errors.length }" placeholder="Enter Username" />
            <p v-for="error in validateOnSubmit.username.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="on-submit-email"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Email</label>
            <input type="text" id="on-submit-email" v-model="onSubmitState.email"
              :class="{ invalid: validateOnSubmit.email.$errors.length }" placeholder="Enter Email" />
            <p v-for="error in validateOnSubmit.email.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="on-submit-password"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Password</label>
            <input type="text" id="on-submit-password" v-model="onSubmitState.password"
              :class="{ invalid: validateOnSubmit.password.$errors.length }" placeholder="Enter Password" />
            <p v-for="error in validateOnSubmit.password.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <button type="submit" class="btn btn-sm btn-primary">Submit</button>
        </form>
      </div>
      <div id="on-blur" class="my-12">
        <h5>On Blur</h5>
        <p>The form is validated on the blur event (e.g. when the user leaves a form field) and the submit button is
          disabled until the form is valid.</p>
        <form novalidate id="on-blur" @submit.prevent="onSubmit(validateOnBlur)">
          <div class="my-6">
            <label for="on-blur-username"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">Username</label>
            <input type="text" id="on-blur-username" v-model="onBlurState.username"
              @blur="validateOnBlur.username.$touch" :class="{ invalid: validateOnBlur.username.$errors.length }"
              placeholder="Enter Username" />
            <p v-for="error in validateOnBlur.username.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="email" class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Email</label>
            <input type="text" id="on-blur-email" v-model="onBlurState.email" @blur="validateOnBlur.email.$touch"
              :class="{ invalid: validateOnBlur.email.$errors.length }" placeholder="Enter Email" />
            <p v-for="error in validateOnBlur.email.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="password" class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Password</label>
            <input type="text" id="on-blur-password" v-model="onBlurState.password"
              @blur="validateOnBlur.password.$touch" :class="{ invalid: validateOnBlur.password.$errors.length }"
              placeholder="Enter Password" />
            <p v-for="error in validateOnBlur.password.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <button type="submit" class="btn btn-sm btn-primary" :disabled="validateOnBlur.$invalid">Submit</button>
        </form>
      </div>
      <div id="on-blur-and-submit" class="my-12">
        <h5>On Blur and Submit</h5>
        <p>The form is validated on the blur event (e.g. when the user leaves a form field) and submit.</p>
        <form novalidate id="on-blur" @submit.prevent="onSubmit(validateOnBlurAndSubmit)">
          <div class="my-6">
            <label for="on-blur-and-submit-username"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">Username</label>
            <input type="text" id="on-blur-and-submit-username" v-model="onBlurAndSubmitState.username"
              @blur="validateOnBlurAndSubmit.username.$touch"
              :class="{ invalid: validateOnBlurAndSubmit.username.$errors.length }" placeholder="Enter Username" />
            <p v-for="error in validateOnBlurAndSubmit.username.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="on-blur-and-submit-email"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Email</label>
            <input type="text" id="on-blur-and-submit-email" v-model="onBlurAndSubmitState.email"
              @blur="validateOnBlurAndSubmit.email.$touch"
              :class="{ invalid: validateOnBlurAndSubmit.email.$errors.length }" placeholder="Enter Email" />
            <p v-for="error in validateOnBlurAndSubmit.email.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <div class="mb-6">
            <label for="on-blur-and-submit-password"
              class="block mb-1 text-sm font-semibold capitalize text-base-800 dark:text-base-200">
              Password</label>
            <input type="text" id="on-blur-and-submit-password" v-model="onBlurAndSubmitState.password"
              @blur="validateOnBlurAndSubmit.password.$touch"
              :class="{ invalid: validateOnBlurAndSubmit.password.$errors.length }" placeholder="Enter Password" />
            <p v-for="error in validateOnBlurAndSubmit.password.$errors" :key="error.$uid"
              class="my-1 text-sm font-semibold text-danger-700 dark:text-danger-500">
              <font-awesome-icon icon="fas fa-circle-info" />
              {{ error.$message }}
            </p>
          </div>
          <button type="submit" class="btn btn-sm btn-primary">Submit</button>
        </form>
      </div>
    </div>
  </section>
  <SectionAside
    :sections="[{ name: 'On Submit', id: '#on-submit' }, { name: 'On Blur', id: '#on-blur' }, { name: 'On Blur and Submit', id: '#on-blur-and-submit' }]" />
</template>
