<script setup>
import { defineProps } from 'vue'

const props = defineProps({
  id: {
    type: String,
    required: true
  },
  name: {
    type: String,
    required: true
  }
})

const copyToClipboard = () => {
  const copyText = document.getElementById(props.id).textContent
  const textArea = document.createElement('textarea')
  textArea.textContent = copyText
  document.body.append(textArea)
  textArea.select()
  document.execCommand('copy')
  textArea.remove()
}
</script>

<template>
  <div class="relative rounded-xl text-base-50 bg-gray-600 dark:bg-base-800 dark:border dark:border-base-600">
    <div class="flex items-center justify-between bg-gray-700 rounded-t-xl dark:bg-base-700">
      <p class="text-gray-100 text-sm font-mono pl-4">{{ props.name }}</p><button
        class="btn btn-sm btn-default text-gray-100 bg-gray-700 rounded-none rounded-tr-xl border-gray-700 hover:bg-gray-600 focus:ring-opacity-10"
        @click="copyToClipboard"
        v-tooltip="{ content: `Copied`, triggers: ['click'], autoHide: true }"><font-awesome-icon icon="fas fa-copy"
          class="text-gray-400 fa-fw mr-1" />Copy</button>
    </div>
    <div :id="props.id" class="p-8 overflow-auto">
      <pre>
<slot />
</pre>
    </div>
  </div>
</template>
