<script setup>
import { onMounted, onBeforeUnmount } from 'vue'
import { RouterView, useRouter } from 'vue-router'
import { useStore } from '@/stores/store'
import HeaderComponent from "./layout/HeaderComponent.vue"
import AsideComponent from "./layout/AsideComponent.vue"
import MainComponent from "./layout/MainComponent.vue";
import ModalComponent from './components/ModalComponent.vue'
import PlaygroundView from './views/PlaygroundView.vue'

const store = useStore()
const router = useRouter()

router.afterEach(() => {
  store.setShowMenu(false)
})

const onResize = () => {
  store.setShowMenu(window.innerWidth > 1023)
}

onMounted(() => {
  window.addEventListener('resize', onResize, { passive: true })
  onResize()
})

onBeforeUnmount(() => {
  window.removeEventListener('resize', onResize)
})
</script>

<template>
  <HeaderComponent />
  <PlaygroundView v-if="false"/>
  <div v-else class="mx-auto max-w-screen-2xl">
    <AsideComponent />
    <div class="lg:ml-[19.5rem]">
      <MainComponent>
        <RouterView />
      </MainComponent>
    </div>
  </div>
  <ModalComponent title="Your payment was successful"
    description="Your payment has been successfully submitted. We’ve sent you an email with all of the details of your order."
    button-text="Got it, Thanks!" />
</template>
