<script setup>
import { useRouter, useRoute } from 'vue-router';
import { defineProps, computed } from 'vue'

defineProps({
  next: {
    type: String,
    default: ''
  },
  nextText: {
    type: String,
    default: `Next`
  },
  previous: {
    type: String,
    default: ''
  },
  previousText: {
    type: String,
    default: `Next`
  }
})

const route = useRoute()
const router = useRouter()

let routes = router.getRoutes()

let routeIndex = computed(() => {
  let index
  for (let i = 0; i < routes.length; i++) {
    if (routes[i].name == route.name) {
      index = i
      break
    }
  }
  return index
})
let nextIndex = computed(() => routeIndex.value + 1)
let prevIndex = computed(() => routeIndex.value - 1)
let isHomeIndex = computed(() => routeIndex.value == 0)
let isFirstIndex = computed(() => routeIndex.value == 1)
let isLastIndex = computed(() => routeIndex.value == routes.length - 1)
</script>

<template>
  <footer v-if="!isHomeIndex"
    class="flex justify-between py-5 lg:py-8 mt-6 lg:mt-12 border-t border-t-base-200 dark:border-base-500"
    :class="isLastIndex ? 'flex-row' : 'flex-row-reverse'">
    <button v-if="!isLastIndex" class="btn btn-sm btn-default" @click="router.push(routes[nextIndex].path)">
      {{ routes[nextIndex]?.name }}
      <font-awesome-icon icon="fas fa-chevron-right" class="ml-2" />
    </button>
    <button v-if="!isFirstIndex" class="btn btn-sm btn-default" @click="router.push(routes[prevIndex].path)">
      <font-awesome-icon icon="fas fa-chevron-left" class="mr-2" /> {{ routes[prevIndex]?.name }}
    </button>
  </footer>
</template>
